<script>
import api from '@/api'
import { getToken } from '@/api/auth'
import Editor from '@/components/Editor'

export default {
  inject: ['reload'],
  data() {
    return {
      data: [],
        formData: {
          type: '1',
          status: '1',
          id: '',
          name: '',
          sname: '',
          website: '',
          img: '',
          img_url: '',
          intro: '',
          type_category: '1',
          editAllPlatType: 1, // 研发商类型 1新增 2编辑
          editAllPlat: '',
      },
      
      ruleValidate: {
        name: [
          { required: true, message: '请输入研发商名称', trigger: 'blur' }
        ],
        sname: [
          { required: true, message: '请输入研发商简称', trigger: 'blur' }
        ],
          
      },
      

      plat_type: '',
      image_url: '',
      curIconFile: {},
      curIconUrl: '',
      curImgMsg: {name: '未选择任何图片'},
      isAdd: false,
      
    }
  },
  created: function() {
    this.isAdd = this.$route.query.id == undefined ? false : true
    this.editAllPlatType = this.$route.query.id ? 2 : 1;
    const token = getToken(),
      id = this.$route.query.id,
      configParams = this.editAllPlatType == 1 ? {token,type:0,} : {token,id,type:1};
    this.getConfig(configParams);
  },
  methods: {
    handleSubmit () {
      this.$Spin.show();
      const token = getToken(),
          id = this.$route.query.id*1 || '',
          name = this.formData.name,
          sname = this.formData.sname,
          website = this.formData.website,
          status = this.formData.status*1,
          img_url = this.formData.img_url,
          img = this.curIconFile,
          content = this.$refs.editorConfig.editor1.txt.html();
    

          const fr = new FormData();
            fr.append("token",token);
            fr.append("id",id);
            fr.append("name",name);
            fr.append("sname",sname);
            fr.append("website",website);
            fr.append("status",status);
            fr.append("img_url",img_url);
            fr.append("content",content);
            fr.append("img",img);
          const params = fr;
      // console.log(params)
      api.exeplat(params).then(res => {
        this.$Spin.hide();
        if(res.code == 0) {
          // console.log(res)
          const timer = setTimeout(() => {
            this.$Modal.remove();
            this.$router.push({"path": '/data/platList'});
          },5000)
          this.$Modal.confirm({
            title: '操作成功',
            content: '5秒后自动跳转研发商管理列表',
            cancelText: '确认',
            okText: '下一篇',
            loading: true,
            onCancel: () => {
              clearTimeout(timer)
              this.$router.push({"path": '/data/platList'});
            },
            onOk: () => {
              clearTimeout(timer)
              this.$Modal.remove();
              this.$router.push({"path": '/data/exeplat'});
            }
          });
        } else {
          this.$Message.error(res.msg)
        }
      })
    },//表单数据提交
    getConfig (configParams) {
      api.addplat(configParams).then(res => {
        if(res.code == 0) {
          this.data = res.data.platInfo
          this.image_url = res.data.config.image_url
          this.id = res.data.id
          this.plat_type = res.data.config.plat_type
          this.configData = res.data.config
          if(this.editAllPlatType == 1) {
            this.formData = {
              name: '',
              sname: '',
              website: '',
              img: '',
              img_url: '',
              intro: '',
              status: '1',
            }
          } else {
            const data = res.data.platInfo
            // console.log(res)
            this.formData = {
              name: data.name || '',
              sname: data.sname || '',
              website: data.website || '',
              img: data.img || '',
              img_url: data.img_url || '',
              intro: data.intro || '',
              tags: data.tags || '',
              status: data.status || '1'
            }
          }
          this.isLoaded = true;
          if (res.data.platInfo == undefined) {
            // console.log(this.curIconUrl)
          } else {
            this.curIconUrl = (res.data.config.image_url + res.data.platInfo.img)
          }
        } else {
          this.$Notice.error({ desc: res.msg })
        }
      })
    },//配置获取
    getFile (e) {
      const file = e.target.files[0],
            type = file.type,
            limitType = 'jpg|jpeg|png',
            limitTypeReg = new RegExp('.('+limitType+')$');

      if (!limitTypeReg.test(type)) {
        this.$Message.warning(`请传${limitType}图片`)
      } else {
        this.curIconFile = e.target.files[0];
        this.curImgMsg = e.target.files[0];
        let fr = new FileReader();
        fr.onload = () => {
          this.curIconUrl = fr.result
        };
        fr.readAsDataURL(this.curImgMsg)
      }
    },//icon file获取
    
  },
  watch: {
    $route (to, from) {
      if(to.fullPath != from.fullPath) {
        this.reload()
      }
    }
  },
  components : {
    Editor
  }
}
</script>

<template>
  <div>
    <h3 v-if="isAdd"><Icon type="ios-add-circle-outline" size=24 />修改研发商</h3>
    <h3 v-else><Icon type="ios-add-circle-outline" size=24 />新增研发商</h3>
    
    <Form ref="formValidate" :model="formData" :rules="ruleValidate" :label-width="100">
      <FormItem label="研发商名称" prop="name">
        <Input  v-model="formData.name" class="namebox"></Input>
      </FormItem>

      <FormItem label="简称" prop="sname" :model="formData">
        <Input v-model="formData.sname" class="snamebox"></Input>
      </FormItem>

      <FormItem label="官网地址" prop="website" :model="formData">
        <Input v-model="formData.website"  class="urlbox"></Input>
        <p class="tips">http/https开头</p>
      </FormItem>

      <FormItem label="状态" >
        <Select v-model="formData.status" style="width:100px;">
            <Option v-for="itm, idx in plat_type" :value="idx" :key="idx">{{ itm }}</Option>
        </Select>
      </FormItem>

      <FormItem label="图片" prop="img" class="big iconBox">
          <div class="uploadBox">
            <div class="upload">
              <Icon type="ios-cloud-upload-outline" size=18></Icon>
              选择文件
            </div>
            <input type="file" name="img" value="" @change="getFile($event)">
          </div>
          <i>{{curImgMsg.name}}</i>
          <img class="defaultImg" :src="curIconUrl" alt="">
        </FormItem>

      <FormItem label="远程图片地址" prop='img_url' :model="formData">
        <Input v-model="formData.img_url"  class="img_urlbox"></Input>
      </FormItem>

      <FormItem label="简介" prop="content">
        <Editor 
          :getEditorContent="formData.intro"
          :getEditorType="1"
          :getEditorId="'editorContainer_1'"
          ref="editorConfig"
          />
      </FormItem>


      <FormItem>
        <Button type="primary" @click="handleSubmit('formData')">提交</Button>
      </FormItem>
    </Form>
    

  </div>
</template>

<style lang="scss" scoped>
  h3 {
    margin-bottom: 20px;
  }
  
  .ivu-select {
    width: 16%;
  }
  .ivu-input-wrapper {
    width: 40%;
  }
  .tips {
    color: orange;
  }
  .urlbox {
    width: 40%;
  }
  .namebox {
    width: 30%;
  }
  .snamebox {
    width: 20%;
  }
  .img_urlbox {
    width: 40%;
  }
  .big {
    width: 50%;
  }
  .iconBox {
    padding-bottom: 20px;
  }
  .uploadBox {
    display: inline-block;
    width: 100px;
    height: 34px;
    border-radius: 4px;
    border: 1px dashed #ccc;
    position: relative;
    input {
      opacity: 0;
      width: 100px;
      height: 32px;
    }
    .upload {
      position: absolute;
      top: 0;
      left: 0;
      width: 100px;
      padding: 0 4px 0 12px;
    }
  }
  .defaultImg {
    position: absolute;
    top: -20px;
    right: 0;
    max-width: 80px;
    border: 1px dashed #ccc;
  }
  .iconBox {
    padding-bottom: 20px;
  }
</style>

<style>
  .w-e-toolbar{
    z-index: 2 !important;
  }
  .w-e-menu {
    z-index: 2 !important;
  }
  .w-e-text-container {
    z-index: 1 !important;
  }
</style>